        }],
        userInfo: {},
        ajaxFg: false
      }
    },
    created() {
      this.userInfo = JSON.parse(localStorage.getItem('userInfo'))
    },
    methods: {
      submit() {
        if (this.ajaxFg) {
          return 
        }
        this.ajaxFg = true
        if (this.form.password !== this.form.again) {
          alert('')
          return
        }
        let params = {
          id: this.userInfo.id,
          password: this.form.password
        }
        this.$http.get('http://112.74.55.229:8090/bc/passworduser.xhtml', {params: params})
        .then((res) => {
          this.ajaxFg = false
          if (res.body.code === 200) {
            alert('')
            history.go(-1)
          } else {
            alert("")
          }
        })
        .catch((err) => {
          this.ajaxFg = false
          alert("")
        })
      }
    },
    components: {
  
    }
  }
</script>

